<template>
  <transition
    name="custom-classes-transition"
    enter-active-class="animated fadeIn"
    leave-active-class="animated fadeOut delay"
  >
    <div id="popover" class="popover__wrap" v-show="isShow" @click="close">
      <transition
        name="custom-classes-transition"
        enter-active-class="animated bounceIn delay"
        leave-active-class="animated bounceOut"
      >
      <div class="popover" v-show="isShow">
        <slot></slot>
      </div>
      </transition>
    </div>
  </transition>
</template>

<script>
  export default {
    props: ['isShow'],
    methods: {
      close(event) {
        if(event.target.id == 'popover') {
          this.$emit('change', false);
        }
      }
    }
  }
</script>


<style lang="scss">
  #popover {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0, 0.3); 
    display: flex;
    align-items: center;
    justify-content: center;

    .popover {
      padding: 0.53rem 0.427rem;
      background: #fff;
      border-radius: 10px;
    }
  }

  .delay {
    animation-delay: 0.2s;
  }
</style>




