<script>
	import Popover from '@/components/Popup/Popover';
	import Cell from '@/components/Cell/Cell';

	export default {
		data() {
			return {
				isShow: false, //是否显示弹窗
				user:'',
				
			}
		},
		computed:{
			//计算年龄
			getAge(){
				let bd =  this.user.birthday
				 // 获得今天的时间
		    let date = new Date();
		    let startDate = new Date(bd);
		    let newDate = date.getTime() - startDate.getTime();
		    return Math.floor(newDate / 1000 / 60 / 60 / 24 / 365);
			},
		},
		
		created() {
			this.getUserDetails();
		},

		methods: {
			showPopover() {
				this.isShow = true;
			},

			//关闭弹窗
			closePopover(val) {
				console.log(val)
				this.isShow = val;
			},
			getUserDetails() {
				this.$Api.UserDetails(0).then((res) => {
					console.log(res);
					if(res.q.s == 0){
						this.user = res.q.user
						let userStr = JSON.stringify(res.q.user)
						localStorage.setItem('UserDetails',userStr)
					}
				})
			}
		},

		components: {
			Popover,
			Cell
		}
	}
